<template>
  <v-content>
    <v-dialog v-if="show" :value="show" persistent width="600">
      <v-dialog v-model="loading" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text class="center">
            Uploading
            <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-sheet>
        <v-form ref="form" v-model="valid" autocomplete="off">
          <v-row justify="center" style="margin:0">
            <h1>Edit Trainer</h1>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">First name</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.firstName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Last name</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.lastName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Email</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.email"
                :full-width="true"
                filled
                :rules="[rules.required,rules.email]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Password</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required]"
                :type="show1 ? 'text' : 'password'"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
                @click:append="show1 = !show1"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Age</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="trainer.age"
                min="0"
                :rules="[rules.required]"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Sex</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="trainer.sex"
                :items="values.sex"
                item-text="name"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Level</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="trainer.level"
                :items="values.level"
                item-text="name"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Active</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox
                v-model="trainer.active"
                style="padding:0 0 25px 40%;height:10px;margin:0"
              ></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Can verify payments</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox
                v-model="trainer.verifyPayments"
                style="padding:0 0 25px 40%;height:10px;margin:0"
              ></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>

          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Payment/h</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="trainer.paymentNumber"
                :full-width="true"
                filled
                :rules="[rules.required]"
                min="0"
                dense
                rounded
                class="fieldStyle"
                :suffix="values.currency[user.currency].symbol"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">Clients</v-col>
            <v-divider vertical></v-divider>
            <v-col class="center">{{trainer.clients.length}}</v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0;" class="mx-auto" align="center" justify="center">
            <v-col class="center">Notes</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-textarea v-model="trainer.notes" filled rounded dense></v-textarea>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
            <v-btn @click="close" color="error" :normal="true">Cancel</v-btn>
            <v-btn color="primary" @click="uploadChanges" :normal="true">Save</v-btn>
          </v-row>
        </v-form>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "editTrainer",
  props: ["show", "trainer"],
  data() {
    return {
      menu2: false,
      valid: false,
      loading: false,
      show1: false,
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        email: v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      }
    };
  },
  computed: mapGetters(["types", "values", "user"]),
  methods: {
    close: function() {
      this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    uploadChanges: async function() {
      //   if (this.$refs.form.validate()) {
      //     this.loading = true;
      //     this.trainer.systemType = this.user.systemType;
      //     await this.$store.dispatch("createTrainer", this.trainer);
      //     await this.$store.dispatch("getTrainers");
      //     this.loading = false;
      //     this.trainer = {};
      //     this.$refs.form.resetValidation();
      //   }
      console.log(this.trainer);
    }
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>