<template>
  <v-content>
    <v-overlay :value="overlay">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>

    <div v-if="overlay === false">
      <addTrainer :show="openTrainerSignUp" @closed="closeTrainerSignUp"></addTrainer>
      <addClient :show="openClientSignUp" @closed="closeClientSignUp"></addClient>
      <editTrainer :show="editTrainer" :trainer="editTrainerData" @closed="closeEditTrainer"></editTrainer>
      <editClient :show="editClient" :client="editClientData" @closed="closeEditClient"></editClient>
      <v-row>
        <v-col style="padding:12px 0px 12px 20px" cols="auto">
          <v-avatar size="80">
            <img :src="user.avatar" />
          </v-avatar>
        </v-col>
        <v-col style="padding:12px 3px 3px 1%" cols="5">
          <v-row align="center" justify="center" no-gutters>
            <h1>Welcome {{types.roles.find(el => el.id == user.role).name}}!</h1>

            <v-col style="padding:0 0 0 5%">
              <settings></settings>
            </v-col>
          </v-row>
          <v-row>
            <h3 style="color:blue">You have 2 clients, 3 new videos and some chat messages</h3>
          </v-row>
        </v-col>
      </v-row>
      <v-row align="start" justify="center" no-gutters>
        <v-col cols="auto" no-gutters>
          <v-row>
            <v-col cols="auto" style="padding-right:0 !important;">
              <v-row style="width:105%">
                <v-btn @click="addTrainer()" block>Add Trainer</v-btn>
              </v-row>

              <v-col style="padding:12px 0 12px 12px; !important max-height: 80vh">
                <v-sheet style="height:80vh;padding-right:12px" class="overflow-y-auto">
                  <v-list style="width:100%;padding-top:0">
                    <v-list-item
                      v-for="(trainer, index) in trainers"
                      :key="index"
                      style="padding:4px"
                    >
                      <v-row
                        @click="trainerData(trainer)"
                        class="hover center"
                        justify="center"
                        align="center"
                      >
                        <v-col style="padding:5px">
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                              <p v-on="on" class="overflowText" style="margin:0;">
                                {{
                                trainer.firstName + " " + trainer.lastName
                                }}
                              </p>
                            </template>
                            <span>
                              {{
                              trainer.firstName + " " + trainer.lastName
                              }}
                            </span>
                          </v-tooltip>
                        </v-col>

                        <v-col md="0" style="padding:12px 0 0 0 ">
                          <v-checkbox
                            disabled
                            style="margin-top:0"
                            label="active"
                            v-model="trainer.active"
                          ></v-checkbox>
                        </v-col>
                      </v-row>
                      <div style="padding:0 0 5px 9%">
                        <v-btn normal :text="true" icon @click="openEditTrainer(trainer)">
                          <v-icon normal color="black">edit</v-icon>
                        </v-btn>
                      </div>
                    </v-list-item>
                  </v-list>
                </v-sheet>
              </v-col>
            </v-col>
            <v-divider vertical></v-divider>
            <v-col cols="auto">
              <v-row>
                <v-btn @click="addClient()" block>Add Clients</v-btn>
              </v-row>
              <v-col style="padding:12px 0 0px 0px; !important max-height: 80vh">
                <v-sheet style="height:80vh;padding-right:0px" class="overflow-y-auto">
                  <v-list style="width:95.5%; padding-top:0">
                    <v-list-item v-for="(client, index) in clients" :key="index">
                      <v-row
                        @click="clientData(client)"
                        class="hover center"
                        justify="center"
                        align="center"
                      >
                        <v-col style="padding:0">
                          <v-tooltip bottom>
                            <template v-slot:activator="{ on }">
                              <p v-on="on" class="overflowText" style="margin-bottom:0">
                                {{
                                client.firstName + " " + client.lastName
                                }}
                              </p>
                            </template>
                            <span>
                              {{
                              client.firstName + " " + client.lastName
                              }}
                            </span>
                          </v-tooltip>
                        </v-col>
                        <v-col md="4" style="padding:15px 0 0 0">
                          <v-checkbox
                            disabled
                            style="margin-top:0"
                            label="active"
                            v-model="client.active"
                          ></v-checkbox>
                          <v-checkbox
                            disabled
                            style="margin-top:0"
                            label="payment"
                            v-model="client.payment"
                          ></v-checkbox>
                        </v-col>
                      </v-row>

                      <div style="padding:0 0 5px 20%">
                        <v-btn normal :text="true" icon @click="openEditClient(client)">
                          <v-icon normal color="black">edit</v-icon>
                        </v-btn>
                      </div>
                    </v-list-item>
                  </v-list>
                </v-sheet>
              </v-col>
            </v-col>
          </v-row>
        </v-col>
        <v-col cols="4">
          <calendar></calendar>
        </v-col>
        <v-col></v-col>
        <v-col cols="3">
          <v-row>
            <!-- <p>φος πήρε ένα δοκίμιο και ανακάτεψε τις λέξεις για να δημιουργήσει ένα δείγμα βιβλίου. Όχι μόνο επιβίωσε πέντε αιώνες, αλλά κυριάρχησε στην ηλεκτρονική στοιχειοθεσία, παραμένοντας με κάθε τρόπο αναλλοίωτο. Έγινε δημοφιλές τη δεκαετία του '60 με την έκδοση των δειγμάτων της Letraset όπου περιελάμβαναν αποσπάσματα του Lorem Ipsum, και πιο πρόσφατα με το λογισμικό ηλεκτρονικής σελιδοποίησης όπως το Aldus PageMaker που περιείχαν εκδοχές του Lorem Ipsum</p> -->
          </v-row>
          <charts></charts>
        </v-col>
      </v-row>
    </div>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import calendar from "../components/calendar";
import addTrainer from "../components/addTrainer";
import addClient from "../components/addClient";
import charts from "../components/charts";
import settings from "../components/settingsAdmin";
import editTrainer from "../components/editTrainer";
import editClient from "../components/editClient";

export default {
  name: "Admin",
  components: {
    calendar,
    addTrainer,
    addClient,
    charts,
    settings,
    editTrainer,
    editClient
  },
  data: () => ({
    openTrainerSignUp: false,
    openClientSignUp: false,
    overlay: false,
    editTrainer: false,
    editClient: false,
    editClientData: {},
    editTrainerData: {},
    clientsArray: []
  }),
  computed: mapGetters([
    "user",
    "trainers",
    "clients",
    "types",
    "activeClient"
  ]),
  async created() {
    this.overlay = true;
    await this.$store.dispatch("getUser");
    await this.$store.dispatch("getTrainers");
    await this.$store.dispatch("getClients");
    await this.$store.commit("activeClient", this.clients[0]);
    await this.$store.dispatch("types");
    await this.$store.dispatch("values");
    this.overlay = false;
  },
  methods: {
    openEditClient(client) {
      this.editClient = true;
      this.editClientData = client;
    },
    closeEditClient() {
      this.editClient = false;
    },

    openEditTrainer(trainer) {
      this.editTrainer = true;
      this.editTrainerData = trainer;
    },
    closeEditTrainer() {
      this.editTrainer = false;
    },
    addTrainer: function() {
      this.openTrainerSignUp = true;
    },
    addClient: function() {
      this.openClientSignUp = true;
    },
    closeTrainerSignUp: function() {
      this.openTrainerSignUp = false;
    },
    closeClientSignUp: function() {
      this.openClientSignUp = false;
    },
    clientData: async function(data) {
      await this.$store.commit("activeClient", data);
      await this.$store.dispatch("getMacros", this.activeClient._id);
    },
    trainerData: async function(data) {
      await this.$store.commit("activeTrainer", data);
    }
  }
};
</script>

<style>
.overflowText {
  display: inline-block;
  width: 120px;
  white-space: nowrap;
  overflow: hidden !important;
  text-overflow: ellipsis;
}

.hover:hover {
  cursor: pointer;
  background: #f5f5f5;
  transition: 0.5s cubic-bezier(0.25, 0.8, 0.5, 1);
}
.center {
  text-align: center;
}
</style>